.PreLoader {
    position: fixed;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.95);
    z-index: 1001;
  }
  
  .PreLoader_image {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 220px;
    height: auto;
    transform: translate(-50%, -50%);
  }
  
